import { Component, OnInit, ChangeDetectorRef, Input, EventEmitter, Output } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from '../../services/auth.service';
import { AccountRecoveryOptionsModel } from '../../../user-management/models/account-recovery-options.model';
import { AuthenticationMethodEnum } from 'projects/web/src/app/shared/enums/authMethodEnum';
import { AccountRecoveryViewModel } from '../../models/account-recovery-view.model';
import { TokenRequestViewModel } from '../../models/token-request-view.model';
import * as _ from 'lodash';

@Component({
  selector: 'app-recovery-options',
  templateUrl: './recovery-options.component.html',
  //styleUrls: ['./validate-token.component.scss']
})
export class RecoveryOptionsComponent implements OnInit {

  @Input() userId: string;
  @Input() msg: string;
  @Input() activation: boolean;

  // an event that's fired when the server sends the token
  @Output() onTokenSend: EventEmitter<boolean> = new EventEmitter<boolean>();
  // an event that's fired when the server does not send the token
  @Output() onFailed: EventEmitter<boolean> = new EventEmitter<boolean>();

  recoveryMethod: FormControl = new FormControl(null, Validators.required);

  tokenSent: boolean = false;
  accountLocked: boolean = false;

  optionsLoaded: boolean = false;
  recoveryOptions: string[];
  recoveryOptionsModel: AccountRecoveryOptionsModel;

  methodDescriptions = {
    email: 'Primary email',
    email2: 'Alternate email',
    phone: 'Phone number'
  };

  constructor(private authService: AuthService, private toastr: ToastrService, private cdRef: ChangeDetectorRef) {

  }

  ngOnInit() {
    this.getOptions();
  }

  getAuthMethod(type: string): number {
    switch (type) {
      case 'email':
        return AuthenticationMethodEnum.EMAIL;
      case 'email2':
        return AuthenticationMethodEnum.EMAIL2;
      case 'phone':
        return AuthenticationMethodEnum.PHONE;
      default:
        return 0;
    }
  }

  getRecoveryOptions(data: AccountRecoveryOptionsModel) {
    return Object.keys(data).filter(k => data[k]);
  }

  getOptions() {
    const model = new AccountRecoveryViewModel();
    model.id = this.userId;
    this.authService.recoveryOptions(model).subscribe((res: AccountRecoveryOptionsModel) => {
      console.log(res);
      if (res.success) {
        this.recoveryOptionsModel = _.cloneDeep(res);
        delete res['success'] && delete res['id'];
        //delete res['success'] && delete res['id'] && delete res['message'];
        this.recoveryOptions = this.getRecoveryOptions(res);
        this.optionsLoaded = true;
        console.log(this.recoveryOptions);
        if (this.recoveryOptions.length === 1) {
          this.recoveryMethod.setValue(this.recoveryOptions[0]);
        }
        if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
      } else {
        this.toastr.error(res.message);
      }
    });
  }

  sendToken() {
    console.log(this.recoveryOptionsModel);
    let model = new TokenRequestViewModel();
    model.id = this.recoveryOptionsModel.id;
    model.method = this.getAuthMethod(this.recoveryMethod.value);
    this.authService.sendToken(model).subscribe(res => {
      console.log(res);
      if (res.success) {
        this.onTokenSend.emit(true);
      }
      else {
        this.recoveryOptions = null;
        this.optionsLoaded = false;
        this.toastr.error(res.message);
        this.onFailed.emit(true);
      }
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }
}
