import { getCustomPatternValidator } from './common-pattern-validator';
import { AbstractControl } from '@angular/forms';

const unameRegEx: RegExp = /[a-zA-Z\d#\-_\~\!\$\&\'\(\)\*\+\,;\=\:]{3,64}/;

export function unameValidator(control: AbstractControl) {
  return getCustomPatternValidator('unameValidator', unameRegEx)(control);
}
