import { Component, OnInit, ChangeDetectorRef, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormControl, Validators, AbstractControl } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { AccountRecoveryOptionsModel } from '../../models/account-recovery-options.model';
import { ForgotPasswordViewModel } from '../../models/forgot-password-view.model';
import { AuthService } from '../../services/auth.service';
import { emailValidator } from 'projects/web/src/app/core/modules/form/validators/email.validator';
import { ForgotUsernameViewModel } from '../../models/forgot-username-view.model';
import { OAuthService } from 'angular-oauth2-oidc';
import { environment } from 'projects/web/src/environments/environment';
import { unameValidator } from 'projects/web/src/app/core/modules/form/validators/username.validator';

@Component({
  selector: 'app-acccount-recovery',
  templateUrl: './account-recovery.component.html',
  styleUrls: ['./account-recovery.component.scss']
})
export class AccountRecoveryComponent implements OnInit {
  @Input() forgotUsername: boolean = false;
  @Input() forgotPassword: boolean = false;

  isLoading: boolean = false;

  msg: string;
  forgotUnameCtrl: FormControl;
  unameSent: boolean = false;

  forgotPwrdCtrl: FormControl;
  linkSent: boolean = false;
  accountLocked: boolean = false;

  captchaCtrl: FormControl;
  captchaKey = environment.recaptchaKey;

  constructor(
    private authService: AuthService,
    private activatedRoute: ActivatedRoute,
    private cdRef: ChangeDetectorRef,
    private toastr: ToastrService,
    private oAuthService: OAuthService,
    private router: Router
  ) {}

  // uname cannot have @
  customPasswordRecoveryValidator = (c: AbstractControl) => (c.value && c.value.includes('@')) ? emailValidator(c) : unameValidator(c);

  ngOnInit() {
    if (this.oAuthService.hasValidAccessToken()) {
      this.router.navigate(['/']);
    }

    this.forgotUnameCtrl = new FormControl(null, [Validators.required, emailValidator]);
    this.forgotPwrdCtrl = new FormControl(null, [Validators.required, this.customPasswordRecoveryValidator]);
    this.captchaCtrl = new FormControl(null, Validators.required);

    this.activatedRoute.queryParams.subscribe(params => {
      //this.userId = params['id'];
      //this.code = params['code'];
    });

    this.activatedRoute.data.subscribe(data => {
      if (data.forgotUsername) {
        this.forgotUsername = true;
      } else if (data.forgotPassword) {
        this.forgotPassword = true;
      }
    });

  }

  forgotUsernameRequest() {
    const email = this.forgotUnameCtrl.value;
    this.forgotUnameCtrl.setErrors({ submitted: true });
    this.forgotUnameCtrl.setValue(null);

    const model = new ForgotUsernameViewModel();
    model.email = email;

    this.isLoading = true;

    this.authService.forgotUsername(model).subscribe(data => {
      if (data.success) {
        this.unameSent = true;
      }
      else {
        this.toastr.error(data.message);
      }
      this.isLoading = false;
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }

  forgotPasswordRequest() {
    const value = this.forgotPwrdCtrl.value;
    this.forgotPwrdCtrl.setErrors({ submitted: true });
    this.forgotPwrdCtrl.setValue(null);

    let model = new ForgotPasswordViewModel();
    if (value.includes('@')) {
      model.email = value;
    } else {
      model.username = value;
    }

    this.isLoading = true;
    this.authService.forgotPassword(model).subscribe(res => {
      console.log(res);
      if (res.success) {
        this.linkSent = true;
      }
      else {
        if(res.locked)
          this.accountLocked = true;
        this.toastr.error(res.message)
      }
      this.isLoading = false;
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }

  onRecaptchaUpdateEvent(event?: any) {
    // console.log(event);
    this.captchaCtrl.updateValueAndValidity();
  }
}
