import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { ResetPasswordComponent } from './components/reset-password/reset-password.component'
import { UserRegistrationComponent } from './pages/user-registration/user-registration.component';
import { AccountRecoveryComponent } from './pages/account-recovery/account-recovery.component';
import { ConfirmRegistrationComponent } from './pages/confirm-registration/confirm-registration.component';

const routes: Routes = [
  {
    path: 'confirm',
    component: ConfirmRegistrationComponent,
  },
  {
    path: 'reset',
    component: ResetPasswordComponent,
  },
  {
    path: 'sign-up',
    component: UserRegistrationComponent
  },
  {
    path: 'forgot-username',
    component: AccountRecoveryComponent,
    data: { forgotUsername: true }
  },
  {
    path: 'forgot-password',
    component: AccountRecoveryComponent,
    data: { forgotPassword: true }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AuthRoutingModule { }
