import { Component, OnInit, ChangeDetectorRef, Input, EventEmitter, Output } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from '../../services/auth.service';
import { TokenRequestViewModel } from '../../models/token-request-view.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-validate-token',
  templateUrl: './validate-token.component.html',
  //styleUrls: ['./validate-token.component.scss']
})
export class ValidateTokenComponent {

  @Input() userId: string;
  @Input() msg: string;
  @Input() activation: boolean;

  // an event that's fired when the server accepts/rejects the token
  @Output() onValidate: EventEmitter<boolean> = new EventEmitter<boolean>();
  // an event that's fired when the server locks a user account
  @Output() onLock: EventEmitter<boolean> = new EventEmitter<boolean>();
  // an event that's fired when the server releases user credentials
  @Output() onRelease: EventEmitter<boolean> = new EventEmitter<boolean>();

  private tokenValidators = [
    Validators.required
  ];

  tokenControl: FormControl = new FormControl('', Validators.compose(this.tokenValidators));
  tokenValidated: boolean = false;
  tokenAttempted: boolean = false;
  tokenResent: boolean = false;

  constructor(private authService: AuthService, private toastr: ToastrService, private cdRef: ChangeDetectorRef) {

  }

  resendToken() {
    let model = new TokenRequestViewModel();
    model.id = this.userId;
    this.tokenResent = true;
    if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    this.authService.sendToken(model).subscribe((res) => {
      if(!res.success) {
        this.toastr.error(res.message);
        if (res.deleted) {
          this.onRelease.emit(true);
        }
        else if (res.locked) {
          this.onLock.emit(true);
        }
      }
    });
  }

  submitForm() {
    const token = this.tokenControl.value;
    if (this.activation) {
      this.authService.confirmActivationToken(this.userId, token).subscribe((res: any) => {
        this.validateToken(res);
      });
    }
    else {
      this.authService.confirmTwoFactorAuthentication(this.userId, token).subscribe((res: any) => {
        this.validateToken(res);
      });
    }
  }

  validateToken(res: any) {
    this.tokenValidated = res.success;

    if (!this.tokenValidated) {
      this.toastr.error(res.message);
      this.tokenAttempted = true;
      this.tokenResent = false;
    }

    this.onValidate.emit(this.tokenValidated);
    if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
  }
}
