import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from '../../services/auth.service';
import { OAuthService } from 'angular-oauth2-oidc';

@Component({
  selector: 'app-confirm-registration',
  templateUrl: './confirm-registration.component.html',
  styleUrls: ['./confirm-registration.component.scss']
})
export class ConfirmRegistrationComponent implements OnInit {
  userId: string;
  code: string = '';
  msg: string;

  isLoading: boolean = true;
  linkExpired: boolean = false;
  linkResent: boolean = false;
  emailConfirmed: boolean = false;
  accountReleased: boolean = false;

  tokenValid: boolean = false;

  constructor(
    private authService: AuthService,
    private activatedRoute: ActivatedRoute,
    private cdRef: ChangeDetectorRef,
    private toastr: ToastrService,
    private oauthService: OAuthService,
    private router: Router
  ) {}

  ngOnInit() {
    if (this.oauthService.hasValidAccessToken()) {
      this.router.navigate(['/']);
    }

    this.activatedRoute.queryParams.subscribe(params => {
      this.userId = params['id'];
      this.code = params['code'];

      this.authService.confirmRegistration(this.userId, this.code).subscribe((data: any) => {
        this.isLoading = false;
        if (data.redirect) this.router.navigate(['/']);
        if (data.success) {
          this.emailConfirmed = true;
          this.toastr.success("Your primary email address has been confirmed.");
        } else {
          this.linkExpired = true;
        }
        if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
      });
    });
  }

  validateToken(event) {
    this.tokenValid = event;
    if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
  }

  handleRelease(event) {
    this.accountReleased = event;
    if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
  }

  redirectToLogin() {
    this.oauthService.initImplicitFlow('/some-state;p1=1;p2=2');
  }

  redirectToRegistration() {
    this.router.navigate(['/auth/sign-up']);
  }

  retryActivation() {
    this.isLoading = true;
    this.authService.retryActivation(this.userId).subscribe((res) => {
      this.isLoading = false;
      if (res.success) {
        this.linkResent = true;
      }
      else {
        this.toastr.error(res.message);
      }
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }

  log() {
    Array.from(arguments).forEach(a => console.log(a));
  }
}
