import { Validators, AbstractControl } from '@angular/forms';
import { CommonFormConstructor } from '../../../core/modules/form/common-form-constructor';
import { emailValidator } from '../../../core/modules/form/validators/email.validator';
import { phoneValidator } from '../../../core/modules/form/validators/phone.validator';

import {uppercaseValidator} from "../../../core/modules/form/validators/uppercase.validator";
import {lowercaseValidator} from "../../../core/modules/form/validators/lowercase.validator";
import {digitValidator} from "../../../core/modules/form/validators/digit.validator";
import {specialValidator} from "../../../core/modules/form/validators/special.validator";

const UserRegistrationFormValidation = {
  userName: [Validators.required, Validators.minLength(3), Validators.maxLength(64)],
  //firstName: [Validators.required],
  //lastName: [Validators.required],
  email: [Validators.required, emailValidator],
  password: [
    Validators.required,
    Validators.minLength(8),
    uppercaseValidator,
    lowercaseValidator,
    digitValidator,
    specialValidator
  ],
  //recaptcha: [Validators.required], // rajiv - commented out as requested by HPD
  confirmPassword: [Validators.required],
  isAgree: [Validators.requiredTrue],
  twoFactorMethod: [Validators.required],

  phone: [phoneValidator],
  email2: [emailValidator]
};

const UserRegistrationFormValidationMessages = {
  'userName.duplicate': 'That username is taken. Please try another one.',
  'email.duplicate': 'This email is already registered',
  'phone.duplicate': 'This phone is already registered',
  'email2.duplicate': 'This email is already registered',
};

export class UserRegistrationFormModel extends CommonFormConstructor {
  userName = '';
  firstName = '';
  lastName = '';
  email = '';
  password = '';
  //recaptcha = ''; // rajiv - commented out as requested by HPD
  confirmPassword = '';
  phone = '';
  email2 = '';
  twoFactorMethod = '';
  isAgree: boolean = false;

  constructor(data?: any) {
    super();
    this.setData(data);
  }

  get validationRules() {
    return UserRegistrationFormValidation;
  }

  get validationMessages() {
    return UserRegistrationFormValidationMessages;
  }
}

export function hasTwoFactorAuthentication(c: AbstractControl) {
  if (!c.get('phone').value && !c.get('email2').value) return { missingTwoFactorAuthentication: true };
  return null;
}

export function passwordsMatch(c: AbstractControl) {
  if (c.get('password').value !== c.get('confirmPassword').value) return { passwordNotConfirmed: true };
  return null;
}

export function emailsDistinct(c: AbstractControl) {
  if (!c.get('email').value || !c.get('email2').value) return null;
  if (c.get('email').value.toLowerCase() === c.get('email2').value.toLowerCase()) return { emailsNotDistinct: true };
  return null;
}
