import { environment } from './../../../../../environments/environment';
import {Component, OnInit, ChangeDetectorRef} from '@angular/core';
import {FormGroup, Validators, FormControl} from '@angular/forms';
import {debounceTime, tap} from 'rxjs/operators';
import {ToastrService} from 'ngx-toastr';
import {FormService} from '../../../../core/modules/form/form-service.service';
import { emailValidator } from '../../../../core/modules/form/validators/email.validator';
import { phoneValidator } from '../../../../core/modules/form/validators/phone.validator';
import { phoneMask } from '../../../../core/addons/textmask-directive-addons';
import {
  UserRegistrationFormModel, hasTwoFactorAuthentication, passwordsMatch, emailsDistinct
} from '../../models/user-registration-form.model';
import {AuthService} from "../../services/auth.service";


@Component({
  selector: 'app-user-registration',
  templateUrl: './user-registration.component.html',
  styleUrls: ['./user-registration.component.scss']
})
export class UserRegistrationComponent implements OnInit {
  isLoadingData: boolean = true;
  userRegistrationForm: FormGroup;
  userNameControl: FormControl;
  emailControl: FormControl;
  phoneControl: FormControl;
  altEmailControl: FormControl;
  twoFactorMethod: FormControl;
  //captchaKey = environment.recaptchaKey;   // rajiv - commented out as requested by HPD

  isPasswordVisible = false;
  isConfirmPasswordVisible = false;
  userCreated: boolean = false;
  isLoading: boolean = false;

  confirmPasswordInputConditionConfig = [
    {condition: true, validatorText: 'match'},
    {condition: false, validatorText: 'no match'}
  ];
  passwordInputConditionConfig = [
    {condition: [Validators.maxLength(0)], validatorText: 'not strong'},
    {condition: [Validators.maxLength(15)], validatorText: 'strong'},
    {condition: [Validators.maxLength(25)], validatorText: 'super strong'}
  ];

  public phoneMask = phoneMask;
  public userRegistrationFormModel = new UserRegistrationFormModel();

  constructor(
    private authService: AuthService,
    private formService: FormService,
    private toastr: ToastrService,
    private cdRef: ChangeDetectorRef
  ) {
  }

  ngOnInit() {
    //this.authService.getUserRegister().subscribe(v => {
    //  console.log(v);
    //});
    this.userRegistrationForm = this.formService.getFormByModel(new UserRegistrationFormModel(), [
      hasTwoFactorAuthentication,
      passwordsMatch,
      emailsDistinct
    ]);

    this.userNameControl = this.userRegistrationForm.get('userName') as FormControl;
    this.emailControl = this.userRegistrationForm.get('email') as FormControl;
    this.phoneControl = this.userRegistrationForm.get('phone') as FormControl;
    this.altEmailControl = this.userRegistrationForm.get('email2') as FormControl;
    this.twoFactorMethod = this.userRegistrationForm.get('twoFactorMethod') as FormControl;
    this.twoFactorMethod.valueChanges.subscribe((value: string) => {
      if (value === "P") {
        this.phoneControl.setValidators([
          Validators.required,
          phoneValidator
        ]);
        this.altEmailControl.setValue(null);
        this.altEmailControl.setValidators([emailValidator]);
      }
      else if (value === "E") {
        this.altEmailControl.setValidators([
          Validators.required,
          emailValidator
        ]);
        this.phoneControl.setValue(null);
        this.phoneControl.setValidators([phoneValidator]);
      }
      this.phoneControl.updateValueAndValidity();
      this.altEmailControl.updateValueAndValidity();
    });

    console.log(this.userRegistrationForm);
  }

  printMessage(data: any) {
    if (!data.success && data.message) {
      this.toastr.error(data.message);
    } else if (data.success && data.message) {
      this.toastr.success(data.message);
    }
  }

  userExists(uname: string, email: string, phone: string, email2: string) {
    return this.authService.userExists(uname, email, phone, email2);
  }

  setDuplicateControl(data: any, c: FormControl) {
    if (!data.success) {
      c.setErrors({duplicate: true});
    } else {
      if (c.hasError('duplicate')) delete c.errors['duplicate'];
    }

    if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
  }

  saveUserForm() {
    let saveData = this.userRegistrationForm.value;
    saveData.phone = (saveData.phone) ? saveData.phone.replace(/(\-|\(|\)|\s)/g, '') : null;
    this.isLoading = true;

    this.authService.saveUser(saveData).subscribe(res => {
      this.userCreated = true;
      this.isLoading = false;
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }

  // rajiv - commented out as requested by HPD
  //onRecaptchaUpdateEvent(event?:any){
  //  // console.log(event);
  //  this.userRegistrationForm.updateValueAndValidity();
  //}

  public isFieldValid(type): boolean {

    //console.log('type', type)
    //console.log('this.userRegistrationForm.get(\'password\').errors',this.userRegistrationForm.get('password').errors)
    const fieldName = (type === 'minlength') ? type : `${type}Validator`;
    return (
      !(this.userRegistrationForm.get('password').errors &&
        this.userRegistrationForm.get('password').errors[fieldName])
    );
  }

  public onInputChange(event, popOver): void {
    if (event.target.value && !popOver.isOpen() && this.userRegistrationForm.get('password').invalid) {
      popOver.open();
      return;
    } else if ((!event.target.value && popOver.isOpen()) || this.userRegistrationForm.get('password').valid) {
      popOver.close();
      return;
    }
  }
}
