import {Component, OnInit, ChangeDetectorRef, ViewChild, ElementRef, OnDestroy} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {FormControl, Validators, FormGroup} from '@angular/forms';
import {ToastrService} from 'ngx-toastr';
import {Subject} from "rxjs";
import {takeUntil} from "rxjs/operators";
import {FormService} from "../../../../core/modules/form/form-service.service";
import {AuthService} from '../../services/auth.service';
import {ResetPasswordFormModel} from '../../models/reset-password-form.model';
import {ResetPasswordViewModel} from "../../models/reset-password-view.model";
import { ForgotPasswordViewModel } from '../../models/forgot-password-view.model';
import { OAuthService } from 'angular-oauth2-oidc';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit, OnDestroy {
  @ViewChild('popOver') popOver: ElementRef;
  private ngUnsubscribe: Subject<void> = new Subject<void>();
  public inputType: string = 'password';

  isLoading: boolean = true;
  userId: string;
  code: string = '';
  msg: string;

  tokenSent: boolean = false;
  tokenValidated: boolean = false;

  resetPasswordForm: FormGroup;
  passwordReset: boolean;
  resetFailed: boolean;
  linkResent: boolean;
  accountLocked: boolean;

  confirmPasswordInputConditionConfig = [
    {condition: true, validatorText: 'match'},
    {condition: false, validatorText: 'no match'}
  ];
  // passwordInputConditionConfig = [
  //   {condition: [Validators.maxLength(0)], validatorText: 'not strong'},
  //   {condition: [Validators.maxLength(15)], validatorText: 'strong'},
  //   {condition: [Validators.maxLength(25)], validatorText: 'super strong'}
  // ];

  constructor(
    private authService: AuthService,
    private activatedRoute: ActivatedRoute,
    private cdRef: ChangeDetectorRef,
    private toastr: ToastrService,
    private formService: FormService,
    private oauthService: OAuthService
  ) {
  }


  public toggleInputType(): void {
    this.inputType = (this.inputType === 'password') ? 'text' : 'password';
  }

  ngOnInit() {
    this.resetPasswordForm = this.formService.getFormByModel(new ResetPasswordFormModel());

    this.activatedRoute.queryParams.subscribe(params => {
      this.userId = params['id'];
      this.code = params['code'];
      this.validateLink();
    });
    console.log(this);
  }

  tokenSendRequest(event: boolean) {
    console.log(event);
    this.tokenSent = event;
    if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
  }

  validateToken(event: boolean) {
    this.tokenValidated = event;
    if (this.tokenValidated) {
      this.validateLink();
    }
  }

  validateLink() {
    this.authService.validateResetPasswordLink(this.userId, this.code).subscribe(result => {
      if (!result) {
        this.resetFailed = true;
      }
      this.isLoading = false;
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }

  handleLock(event) {
    this.resetFailed = this.accountLocked = event;
    if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
  }

  resendLink() {
    const model = new ForgotPasswordViewModel();
    model.id = this.userId;

    this.isLoading = true;
    this.authService.forgotPassword(model)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(result => {
        if (result.success) {
          this.linkResent = true;
        }
        else {
          this.accountLocked = true
          this.toastr.error(result.message);
        }
        this.isLoading = false;
        if(!this.cdRef['destroyed']) this.cdRef.detectChanges();
      });
  }

  resetPassword() {
    console.log(this.resetPasswordForm.value);
    const model = new ResetPasswordViewModel({
      userId: this.userId,
      password: this.resetPasswordForm.get('password').value,
      confirmPassword: this.resetPasswordForm.get('confirmPassword').value,
      code: this.code,
    });

    this.authService.resetPassword(model, this.code)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(result => {
        if (!result.success) {
          this.resetFailed = true;
          this.toastr.error(result.message);
          return;
        }
        this.passwordReset = true;
        this.cdRef.detectChanges();
      });
  }

  public onInputChange(event, popOver): void {
    if (event.target.value && !popOver.isOpen()) {
      popOver.open();
      return;
    } else if (!event.target.value && popOver.isOpen()) {
      popOver.close();
      return;
    }
  }

  public isFieldValid(type): boolean {
    const fieldName = (type === 'minlength') ? type : `${type}Validator`;
    return (
      !(this.resetPasswordForm.get('password').errors &&
        this.resetPasswordForm.get('password').errors[fieldName])
    );
  }

  redirectToLogin() {
    this.oauthService.initImplicitFlow('/some-state;p1=1;p2=2');
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
